@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&family=Share+Tech&display=swap);
/* Style entire website */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Changes text-highlight colour */
::selection {
  background: #f8ba45;
}

/* Style font for body */
body {
  font-family: "Lato", sans-serif;
}

/* Add padding to h2*/
h2 {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 2em;
}

/* Style header used for navigation */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

/* Position links */
.header__link {
  float: left;
  color: black;
  text-align: center;
  margin: 12px;
  text-decoration: none;
  font-size: 1em;
  line-height: 25px;
  cursor: pointer;
}

/* Style header links */
.header__link:hover {
  margin-bottom: 2px;
  border-bottom: 2px solid #f8ba45;
  color: #113969;
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

/* Style header logo  */
.logo {
  float: left;
  color: black;
  text-align: center;
  margin: 12px;
  text-decoration: none;
  font-size: 1em;
  line-height: 25px;
  cursor: pointer;
  font-size: 1.5em;
  color: #113969;
}

/* Setting container size */
.container {
  margin: auto 20rem auto 20rem;
  font-size: 1.2em;
  word-wrap: normal;
}

/* Style about a tags */
.about__link {
  color: #113969;
}

/* Adding margin for post */
.post {
  margin: 10px auto 10px auto;
}

/* Style footer */
footer {
  margin: 200px auto 100px auto;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  color: #113969;
}

/* Style footer font size */
footer p {
  font-size: 1.3em;
}

/* Style footer icon */
.footer-icon {
  margin: 10px 5px auto 5px;
  color: #113969;
  font-size: 1.3em;
}

/* Style footer icon hover */
.footer-icon:hover {
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
  color: #2d67ae;
}

/* width */
::-webkit-scrollbar {
  width: 1em;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #ffffff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 2em;
  border: 4.5px solid transparent;
  background-clip: content-box;
  background-color: #113969;
}

/* Handle */
::-webkit-scrollbar-thumb:hover {
  border-radius: 2em;
  border: 4.5px solid transparent;
  background-clip: content-box;
  background-color: #2d67ae;
}

/* Adjusts margin based on window size (max-width of 1500px) */
@media only screen and (max-width: 1500px) {
  .container {
    margin-left: 10rem;
    margin-right: 10rem;
  }
}

/* Adjusts margin based on window size (max-width of 1000px) */
@media only screen and (max-width: 1000px) {
  .container {
    margin-left: 8rem;
    margin-right: 8rem;
  }
}

/* Adjusts margin based on window size (max-width of 700px) */
@media only screen and (max-width: 700px) {
  .container {
    margin-left: 4rem;
    margin-right: 4rem;
  }
}

/* Style title container */
.title__container {
  margin: 13rem 0 17rem 0;
}

/* Style intro title */
.title {
  font-size: 3em;
  font-family: "Lato", sans-serif;
  font-weight: 400;
}

/* Set waving animation for emoji */
.wave {
  -webkit-animation-name: wave-animation;
          animation-name: wave-animation; /* Refers to the name of your @keyframes element below */
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s; /* Change to speed up or slow down */
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; /* Never stop waving :) */
  -webkit-transform-origin: 70% 70%;
          transform-origin: 70% 70%; /* Pivot around the bottom-left palm */
  display: inline-block;
}

/* Keyframe animation */
@-webkit-keyframes wave-animation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  10% {
    -webkit-transform: rotate(14deg);
            transform: rotate(14deg);
  } /* The following five values can be played with to make the waving more or less extreme */
  20% {
    -webkit-transform: rotate(-8deg);
            transform: rotate(-8deg);
  }
  30% {
    -webkit-transform: rotate(14deg);
            transform: rotate(14deg);
  }
  40% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  50% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  60% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  } /* Reset for the last half to pause */
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes wave-animation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  10% {
    -webkit-transform: rotate(14deg);
            transform: rotate(14deg);
  } /* The following five values can be played with to make the waving more or less extreme */
  20% {
    -webkit-transform: rotate(-8deg);
            transform: rotate(-8deg);
  }
  30% {
    -webkit-transform: rotate(14deg);
            transform: rotate(14deg);
  }
  40% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  50% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  60% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  } /* Reset for the last half to pause */
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

/* Style intro button */
.intro__link {
  background-color: white;
  cursor: pointer;
  padding: 18px;
  display: flex;
  align-items: center;
  border-width: 1px;
  border-radius: 5px;
  border-color: #ffffff;
  outline: none;
  font-family: "Lato", sans-serif;
  font-size: 1.5em;
  font-weight: 700;
  color: #113969;
  box-shadow: 0 0px 4px 1px #113969;
  transition: all 0.3s ease-in-out;
  width: 10.5rem;
  text-decoration: none;
  margin-top: 1rem;
}

/* Create the hidden pseudo-element */
/* include the shadow for the end state */
.intro__link::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  border-radius: 5px;
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

/* Scale up the button */
.intro__link:hover {
  -webkit-transform: scale(1.01, 1.01);
          transform: scale(1.01, 1.01);
  box-shadow: 0 10px 20px -5px #113969;
}

/* Fade in the pseudo-element with the bigger shadow */
.intro__link:hover::after {
  opacity: 1;
}

/* Style accordion section */
.accordion__section {
  display: flex;
  flex-direction: column;
  border-style: solid;
  border-width: 1px;
  border-radius: 5px;
  border-color: #ffffff;
  box-shadow: 0 0px 4px 1px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease-in-out;
}

/* Create the hidden pseudo-element */
/* include the shadow for the end state */
.accordion__section::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  border-radius: 5px;
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

/* Scale up the box */
.accordion__section:hover {
  -webkit-transform: scale(1.01, 1.01);
          transform: scale(1.01, 1.01);
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.3);
}

/* Fade in the pseudo-element with the bigger shadow */
.accordion__section:hover::after {
  opacity: 1;
}

/* Style buttons used to open and close accordion panel */
.accordion {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  cursor: pointer;
  padding: 18px;
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  transition: background-color 0.25s ease;
}

/* Add a background color to the button if it is clicked on  */
.accordion:hover,
.active {
  background-color: rgb(255, 255, 255);
}

/* Style the accordion content title */
.accordion__title {
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 1.5em;
  text-align: left;
}

/* Style accordion chevron icon */
.accordion__icon {
  margin-left: auto;
  transition: -webkit-transform 0.25s ease;
  transition: transform 0.25s ease;
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
  color: #113969;
  font-size: 1.3em;
}

/* Style to rotate icon when state is active */
.rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

/* Style the accordion content panel. Note: hidden by default */
.accordion__content {
  background-color: white;
  overflow: hidden;
  transition: max-height 0.25s ease;
}

/* Style the accordion content date */
.accordion__date {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-style: italic;
  padding: 10px 18px 10px 18px;
}

/* Style the accordion content text */
.accordion__text {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  padding: 10px 18px 10px 18px;
}

/* Style the accordion content text container*/
.accordion__tech__container {
  margin-left: 18px;
  margin-top: 10px;
}

/* Style the accordion content text */
.accordion__tech {
  font-family: "Share Tech", sans-serif;
  font-weight: 400;
  color: rgb(255, 255, 255);
  padding: 5px;
  background-color: #2d67ae;
  margin-right: 5px;
  border-radius: 2px;
}

/* Style accordion links container */
.accordion__links__container {
  margin-left: 18px;
  margin-bottom: 10px;
}

/* Style accordion links */
.accordion__links {
  margin-right: 0.5rem;
  color: #113969;
  font-size: 1.3em;
}

/* Style accordion links hover */
.accordion__links:hover {
  color: #2d67ae;
}

